.container {
  display: flex;
  justify-content: center;
}

.card {
  display: flex;
  height: 420px;
  width: 300px;
  background-color: #17141d;
  border-radius: 10px;
  box-shadow: -1rem 0 3rem #000;
  /*   margin-left: -50px; */
  transition: 0.4s ease-out;
  position: relative;
  left: 0px;
  color: white;
  font-size: 20px;
  padding-top: 10px;
}

.deleteicon {
  cursor: pointer;
  /* box-shadow: 0px 0px 8px 8px rgba(0, 0, 0, 1);
  -webkit-box-shadow: 0px 0px 8px 8px rgba(0, 0, 0, 1);
  -moz-box-shadow: 0px 0px 8px 8px rgba(0, 0, 0, 1); */

  border-radius: 25px;
}

.deleteicon:hover {
  transform: scale(1.3);
  transition: transform 0.2s ease-in-out;
}

/* .card:not(:first-child) {
  margin-left: -50px;
} */

.card:hover {
  transform: translateY(-20px);
  transition: 0.4s ease-out;
  z-index: 2;
}

.card:hover ~ .card {
  position: relative;
  left: 50px;
  transition: 0.4s ease-out;
}

.title {
  color: white;
  font-weight: 300;
  position: absolute;
  left: 20px;
  top: 15px;
}

.bar {
  position: absolute;
  top: 100px;
  left: 20px;
  height: 5px;
  width: 150px;
}

.emptybar {
  background-color: #2e3033;
  width: 100%;
  height: 100%;
}

.filledbar {
  position: absolute;
  top: 0px;
  z-index: 3;
  width: 0px;
  height: 100%;
  background: rgb(0, 154, 217);
  background: linear-gradient(
    90deg,
    rgba(0, 154, 217, 1) 0%,
    rgba(217, 147, 0, 1) 65%,
    rgba(255, 186, 0, 1) 100%
  );
  transition: 0.6s ease-out;
}

.card:hover .filledbar {
  width: 120px;
  transition: 0.4s ease-out;
}

.circle {
  position: absolute;
  top: 150px;
  left: calc(50% - 60px);
}

.stroke {
  stroke: white;
  stroke-dasharray: 360;
  stroke-dashoffset: 360;
  transition: 0.6s ease-out;
}

svg {
  fill: #17141d;
  stroke-width: 2px;
}

.card:hover .stroke {
  stroke-dashoffset: 100;
  transition: 0.6s ease-out;
}

.google {
  z-index: 20;
  color: white;
  font-size: 15px;
  font-weight: bold;
}
